import {Scene, PerspectiveCamera, WebGLRenderer, AmbientLight, DirectionalLight, HemisphereLight, GammaEncoding, Object3D, Vector3, Box3} from "three";
import {CSS3DRenderer, CSS3DSprite} from "three/examples/jsm/renderers/CSS3DRenderer";
import {CameraControls} from "./CameraControls";
import {SceneLoader} from "./SceneLoader";
import {VignetteBackground} from "./VignetteBackground";
import {Convergence, Easing} from "utils/Convergence";
import {BoundedConvergence} from "utils/BoundedConvergence";
import {Constants} from "utils/Constants";
import {Signal} from "utils/Signal";
import {Container} from "./Container";
import {IContainer, Model} from "model/Model";
import {ObjectUtils} from "utils/ObjectUtils";
import {THREEUtils} from "utils/THREEUtils";
import {SelectionTool} from "./SelectionTool";
import {HTMLUtils} from "utils/HTMLUtils";
import {VectorUtils} from "utils/VectorUtils";
import {StringUtils} from "utils/StringUtils";
import {ModelFormat, SceneExporter} from "utils/SceneExporter";

export class SceneManager
{
	private _model: Model;
	private _canvas: HTMLCanvasElement;
	private _css3DScene: Scene = new Scene();
	private _scene: Scene = new Scene();
	private _activeDropDown: HTMLElement = null;
	private _containerGroup: Object3D = new Object3D();
	private _originalContainers: Object3D[] = [];
	private _possibleDirections = {
		left: [-1, 0, 0],
		right: [1, 0, 0],
		top: [0, 1, 0],
		front: [0, 0, 1],
		back: [0, 0, -1]
	};
	private readonly _containers: Container[] = [];
	private _camera: PerspectiveCamera = new PerspectiveCamera(60, window.innerWidth / window.innerHeight, 0.01, 1000);
	private _controls: CameraControls;
	private _selectionTool: SelectionTool;
	private _css3DRenderer: CSS3DRenderer;
	private _renderer: WebGLRenderer;
	private _distance: BoundedConvergence = new BoundedConvergence(10, 10, 1, 100, Easing.EASE_OUT, Constants.ANIMATION_DURATION);
	private _target = {
		x: new Convergence(0, 0),
		y: new Convergence(0, 0),
		z: new Convergence(0, 0)
	}
	private _normalizedCameraPosition: number[] = [0, 0, 1];
	private _isInLoop: boolean = false;
	private static _timeStamp: number = 0;
	private static _deltaFrame: number = 1000;
	public static prevTimeStamp: number = 0;

	public needsRender = true;

	public signals = {
		onBeforeRender: Signal.create<number>(),
		onContainerSelected: Signal.create<IContainer>()
	};

	constructor(model: Model)
	{
		this._model = model;
		this._canvas = document.getElementById("myCanvas") as HTMLCanvasElement;
	}

	public async init()
	{
		this.initListeners();
		this.initBackground();
		this.initLights();
		this.initControls();
		this.initRenderers();
		await this.initMeshes();
		this.onWindowResize();
		this.startLoop();
	}

	private initListeners()
	{
		this._model.signals.containerAdded.add(this.onContainerAdded);
		this._model.signals.containerRemoved.add(this.onContainerRemoved);
		this._model.signals.configLoaded.add(this.onConfigLoaded);

		window["exportScene"] = () =>
		{
			const containerGroupClone = this.getCenteredContainerClone();

			SceneExporter.downloadAsGLB(containerGroupClone);
		};
	}

	private getCenteredContainerClone()
	{
		const containerGroupClone = this._containerGroup.clone();
		const bboxOfPivotPoints = new Box3().setFromPoints(containerGroupClone.children.map(v => v.position));
		const center = THREEUtils.getCenterOfBoundingBox(bboxOfPivotPoints);
		const offset = new Vector3().set(center.x, 0, center.z);
		for (const container of containerGroupClone.children)
		{
			container.position.sub(offset);
			THREEUtils.updateMatrices(container);
		}

		return containerGroupClone;
	}

	public async getObjectUrlFromConfig(format: ModelFormat)
	{
		const containerGroupClone = this.getCenteredContainerClone();

		const url = await SceneExporter.getUrlFromScene(containerGroupClone, format);

		return url;
	}

	// Including the selected one
	public getContainersAboveSelected()
	{
		const containersInColumn: IContainer[] = [];

		const {selectedContainer} = this._selectionTool;
		if (selectedContainer)
		{
			containersInColumn.push(selectedContainer);
			for (const container of this._model.containers)
			{
				if (container.location[0] === selectedContainer.location[0] &&
					container.location[2] === selectedContainer.location[2] &&
					container.location[1] >= selectedContainer.location[1] &&
					!containersInColumn.includes(container))
				{
					containersInColumn.push(container);
				}
			}
		}
		
		return containersInColumn;
	}

	private onConfigLoaded = (containers: IContainer[]) =>
	{
		// destroy everything we currently have on the scene
		for (const container of this._containers)
		{
			container.destroy();
		}
		this._containers.length = 0;

		// Add new containers from the config
		for (const containerModelData of containers)
		{
			const newContainer = new Container(this, containerModelData);
			this._containers.push(newContainer);
		}

		this.deselectAll();
	};

	private onContainerAdded = (containerModelData: IContainer) =>
	{
		const newContainer = new Container(this, containerModelData);
		this._containers.push(newContainer);

		this.selectContainer(containerModelData);
	};

	private onContainerRemoved = (containerModelData: IContainer) =>
	{
		const {container, index} = this.getContainerAtLocation(containerModelData.location);
		if (index > -1)
		{
			this._containers.splice(index, 1);

			container?.destroy();

			this.deselectAll();
		}
	};

	private getContainerAtLocation(location: number[]): {container: Container, index: number}
	{
		let container = null;
		const index = this._containers.findIndex((container: Container) => ObjectUtils.compare(location, container.modelData.location));
		if (index > -1)
		{
			container = this._containers[index];
		}

		return {
			container,
			index
		};
	}

	private initBackground()
	{
		this._scene.add(new VignetteBackground({
			aspect: this._camera.aspect,
			grainScale: Constants.IS_IOS ? 0 : 0.001, // mattdesl/three-vignette-background#1
			colors: ["#ffffff", "#353535"]
		}).mesh);
	}


	private initLights()
	{
		const light1 = new AmbientLight(0xFFFFFF, 0.1);

		const light2 = new DirectionalLight(0xFFFFFF, 0.1);
		light2.position.set(0.5, 0, 0.866); // ~60ยบ

		const light3 = new HemisphereLight(0xffffff, 0x080810, 0.8);

		this._scene.add(light1, light2, light3);
	}

	private initControls()
	{
		this._controls = new CameraControls(this.domElement, this);
		this._selectionTool = new SelectionTool(this);
	}

	private async initMeshes()
	{
		this._scene.add(this._containerGroup);
		const containers = await SceneLoader.loadGLTF("assets/models/containers.glb");

		const typeNames = SceneManager.types.map(StringUtils.convertLabelToId);

		for (const typeName of typeNames)
		{
			this._originalContainers.push(containers.getObjectByName(typeName));
		};

		// this._model.addContainer({location: [0, 0, 0], type: 1});
		this.deselectAll();
	}

	private isThereAContainerBelowThisOne(location: number[])
	{
		const containerBelow = this._model.containers.find((container: IContainer) => (
			container.location[0] === location[0] &&
			container.location[2] === location[2] &&
			container.location[1] === location[1] - 1
		));

		return !!containerBelow;
	}

	public selectContainer(containerModelData: IContainer)
	{
		this.clearCSS3DScene();

		if (containerModelData)
		{
			const {container} = this.getContainerAtLocation(containerModelData.location);
			this.focusOnContainer(container);

			// Set up css3d elements

			for (const direction in this._possibleDirections)
			{
				const selectedLocation = container.modelData.location;
				const potentialLocation = VectorUtils.add(selectedLocation, this._possibleDirections[direction]);

				const existingContainerObject = this.getContainerAtLocation(potentialLocation);

				if (!existingContainerObject.container)
				{
					if (potentialLocation[1] === 0 || this.isThereAContainerBelowThisOne(potentialLocation))
					{
						const button = HTMLUtils.createAddButton((event: MouseEvent | TouchEvent) =>
						{
							event.preventDefault();
							event.stopImmediatePropagation();

							this.updateActiveDropDown(potentialLocation);

							button.appendChild(this._activeDropDown);
						});

						const sprite = THREEUtils.createSpriteFromButton(button, container, this._possibleDirections[direction])
						this._css3DScene.add(sprite);
					}
				}
			}

			this._selectionTool.selectContainer(containerModelData);

			this.signals.onContainerSelected.dispatch(containerModelData);
		}
		else
		{
			this.deselectAll();
		}
	}

	private updateActiveDropDown(potentialLocation: number[])
	{
		if (this._activeDropDown)
		{
			HTMLUtils.detach(this._activeDropDown);
		}

		this._activeDropDown = this.createDropDown(potentialLocation);
	}

	public createDropDown(potentialLocation: number[], addLabel: boolean = false)
	{
		const typeLabels = SceneManager.types;
		const list: {[key: string]: () => void} = {};

		for (let i = 0; i < typeLabels.length; ++i)
		{
			const typeLabel = typeLabels[i];
			list[typeLabel] = () => this._model.addContainer({location: potentialLocation, type: i + 1});
		}

		return HTMLUtils.createAddDropDown(list, addLabel);
	}

	public deselectAll()
	{
		this._selectionTool.selectContainer(null);
		if (this._containers.length > 0)
		{
			const bbox = new Box3();
			for (const container of this._containers)
			{
				bbox.expandByObject(container.intersectable);
			}

			const center = THREEUtils.getCenterOfBoundingBox(bbox);
			this.setCameraTarget(center.x, center.y, center.z);
		}
		else
		{
			this.setCameraTarget(0, 0, 0);
		}

		this.signals.onContainerSelected.dispatch(null);

		this.clearCSS3DScene();
	}

	private clearCSS3DScene()
	{
		this._css3DScene.traverse((object3D: Object3D) => 
		{
			if (object3D instanceof CSS3DSprite)
			{
				HTMLUtils.detach(object3D.element)
			}
		});

		this._css3DScene.children.length = 0;
	}

	private adjustCSS3DElements()
	{
		this._css3DScene.traverse((object: Object3D) =>
		{
			if (object instanceof CSS3DSprite)
			{
				const scaleValue = this._distance.value * 0.0035;
				object.scale.set(scaleValue, scaleValue, scaleValue);

				if (this._selectionTool.isPointCovered(object.position))
				{
					object.element.classList.add("disabled");
				}
				else
				{
					object.element.classList.remove("disabled");
				}
			}
		});
	}

	private focusOnContainer(container: Container)
	{
		if (container)
		{
			const pos = container.position;
			this.setCameraTarget(pos.x, pos.y, pos.z);
		}
	}

	private setCameraTarget(x: number, y: number, z: number)
	{
		this._target.x.setEnd(x);
		this._target.y.setEnd(y);
		this._target.z.setEnd(z);
	}

	private initRenderers()
	{
		const contextAttributes = {
			alpha: false,
			antialias: true
		};
		const context = this._canvas.getContext("webgl2", contextAttributes) || this._canvas.getContext("experimental-webgl2", contextAttributes);
		this._renderer = new WebGLRenderer({
			canvas: this._canvas,
			context: context as WebGL2RenderingContext,
			...contextAttributes
		});
		this._renderer.setPixelRatio(window.devicePixelRatio);
		this._renderer.setClearColor(0xECF8FF);
		this._renderer.outputEncoding = GammaEncoding;

		this._canvas.addEventListener("webglcontextlost", this.onContextLost);

		this._css3DRenderer = new CSS3DRenderer();
		this._css3DRenderer.domElement.style.position = "absolute";
		this._css3DRenderer.domElement.style.top = "0";
		this._css3DRenderer.domElement.style.left = "0";
		this._canvas.parentElement.appendChild(this._css3DRenderer.domElement);

		window.addEventListener("resize", this.onWindowResize);
	}

	private onWindowResize = () =>
	{
		this._canvas.width = 0;
		this._canvas.height = 0;

		const width = window.innerWidth;
		const height = window.innerHeight;

		this._renderer.setSize(width, height);
		this._css3DRenderer.setSize(width, height);
		this._camera.aspect = width / height;
		this._camera.updateProjectionMatrix();

		this.needsRender = true;
	};

	private onContextLost = (event: Event) =>
	{
		event.preventDefault();

		alert("Unfortunately WebGL has crashed. Please reload the page to continue!");
	};

	private update = () =>
	{
		SceneManager._timeStamp = performance.now();
		SceneManager._deltaFrame = SceneManager._timeStamp - SceneManager.prevTimeStamp;
		SceneManager.prevTimeStamp = SceneManager._timeStamp;
		this.needsRender = Convergence.updateActiveOnes(SceneManager._timeStamp) || this.needsRender;
		if (this.needsRender)
		{
			this._normalizedCameraPosition = this._controls.update();
			this._camera.position.set(
				this._target.x.value + this._normalizedCameraPosition[0] * this._distance.value,
				this._target.y.value + this._normalizedCameraPosition[1] * this._distance.value,
				this._target.z.value + this._normalizedCameraPosition[2] * this._distance.value
			);
			this._camera.lookAt(
				this._target.x.value,
				this._target.y.value,
				this._target.z.value
			);
			this.adjustCSS3DElements();
			this.signals.onBeforeRender.dispatch(SceneManager._timeStamp);
			this._css3DRenderer.render(this._css3DScene, this._camera);
			this._renderer.render(this._scene, this._camera);
			this.needsRender = false;
		}
	};

	private startLoop()
	{
		if (!this._isInLoop)
		{
			this._isInLoop = true;
			this.update();
			this._renderer.setAnimationLoop(this.update);
		}
	}

	public createNewContainer(type: number)
	{
		return this._originalContainers[type - 1].clone();
	}

	public get scene()
	{
		return this._scene;
	}

	/** Returns the timestamp of the newest render run  */
	public static get timeStamp()
	{
		return SceneManager._timeStamp;
	}

	/** Returns the time between the last 2 frames, so we can get an idea of the user's FPS */
	public static get deltaFrame()
	{
		return SceneManager._deltaFrame;
	}

	public get distance()
	{
		return this._distance;
	}

	public get camera()
	{
		return this._camera;
	}

	public get domElement()
	{
		return this._canvas.parentElement;
	}

	public get containerGroup()
	{
		return this._containerGroup;
	}

	public get containers()
	{
		return this._containers;
	}

	public get cameraControls()
	{
		return this._controls;
	}

	public get selectionTool()
	{
		return this._selectionTool;
	}

	public static get types()
	{
		return [
			"Type 1",
			"Type 2",
			"Type 3",
			"Type 4",
			"Type 5",
			"Type 6",
			"Type 7",
			"Type 8"
		];
	}
}