import {IContainer} from "model/Model";
import {Intersection, Mesh, Raycaster, Vector2, Vector3} from "three";
import {Container} from "./Container";
import {SceneManager} from "./SceneManager";

export class SelectionTool
{
	private _isActive: boolean = false;
	private _sceneManager: SceneManager;
	private _raycaster: Raycaster = new Raycaster();
	private _cursorCoords: Vector2 = new Vector2();
	private _selectedContainer: IContainer = null;
	private _hoveredContainer: IContainer = null;
	private _intersectables: Mesh[] = [];
	private _intersections: Intersection[] = [];

	constructor(sceneManager: SceneManager)
	{
		this._sceneManager = sceneManager;
	}

	private get _domElement()
	{
		return this._sceneManager.domElement;
	}

	public activate()
	{
		if (!this._isActive)
		{
			this._isActive = true;

			this._domElement.addEventListener("mousedown", this.onMouseDown);
			this._domElement.addEventListener("touchstart", this.onTouchStart);

			window.addEventListener("mousemove", this.onMouseModified);
			window.addEventListener("touchmove", this.onTouchModified);

			this._sceneManager.signals.onBeforeRender.add(this.onBeforeRender);
			this._sceneManager.cameraControls.signals.click.add(this.onClick);
		}
	}

	public deactivate()
	{
		if (this._isActive)
		{
			this._isActive = false;

			this._domElement.removeEventListener("mousedown", this.onMouseDown);
			this._domElement.removeEventListener("touchstart", this.onTouchStart);

			window.removeEventListener("mousemove", this.onMouseModified);
			window.removeEventListener("touchmove", this.onTouchModified);

			this._sceneManager.signals.onBeforeRender.remove(this.onBeforeRender);
			this._sceneManager.cameraControls.signals.click.remove(this.onClick);
		}
	}

	private onMouseDown = (event: MouseEvent) =>
	{
		this.onMouseModified(event);
	};

	private onTouchStart = (event: TouchEvent) =>
	{
		this.onTouchModified(event);
	};

	private onMouseModified = (event: MouseEvent) =>
	{
		this.updateCoords(event.clientX, event.clientY);
	};

	private onTouchModified = (event: TouchEvent) =>
	{
		if (event.touches.length === 1)
		{
			const firstTouch = event.touches[0];
			this.updateCoords(firstTouch.clientX, firstTouch.clientY);
		}
	};

	private updateCoords(x: number, y: number)
	{
		this._cursorCoords.x = (x / this._domElement.offsetWidth) * 2 - 1;
		this._cursorCoords.y = - (y / this._domElement.offsetHeight) * 2 + 1;

		this._sceneManager.needsRender = true;
	}

	private updateIntesections()
	{
		this._intersections.length = 0;
		this._intersectables = this._sceneManager.containers.map((container: Container) => container.intersectable);
		this._raycaster.intersectObjects(this._intersectables, false, this._intersections);

		return this._intersections;
	}

	private calculateHoveredContainer()
	{
		this._raycaster.setFromCamera(this._cursorCoords, this._sceneManager.camera);

		this._intersections = this.updateIntesections();

		if (this._intersections.length > 0)
		{
			if (this._intersections[0].object.userData?.modelData)
			{
				return this._intersections[0].object.userData?.modelData;
			}
		}

		return null;
	}

	private onBeforeRender = (timeStamp: number)	=>
	{
		this._hoveredContainer = this.calculateHoveredContainer();
	};

	private onClick = (params: {clientX: number, clientY: number}) =>
	{
		this.updateCoords(params.clientX, params.clientY);

		this._hoveredContainer = this.calculateHoveredContainer();
		this._selectedContainer = this._hoveredContainer;

		this._sceneManager.selectContainer(this._selectedContainer);
	};

	public isPointCovered(point: Vector3)
	{
		const direction = new Vector3().subVectors(point, this._sceneManager.camera.position);
		this._raycaster.set(this._sceneManager.camera.position, direction.clone().normalize());
		this._intersections = this.updateIntesections();

		if (this._intersections.length > 0)
		{
			if (this._intersections[0].distance < direction.length())
			{
				return true;
			}
		}

		return false;
	}

	public selectContainer(containerModelData: IContainer)
	{
		this._selectedContainer = containerModelData;
	}

	public get selectedContainer()
	{
		return this._selectedContainer;
	}
}