import {GLTFExporter} from "three/examples/jsm/exporters/GLTFExporter";
import {USDZExporter} from "three/examples/jsm/exporters/USDZExporter";
import {Object3D} from "three";
import {DateUtils} from "./DateUtils";
import {FileUtils} from "./FileUtils";
import {THREEUtils} from "./THREEUtils";

export type ModelFormat = "glb" | "usdz";

export class SceneExporter
{
	private static _gltfExporter = new GLTFExporter();
	private static _usdzExporter = new USDZExporter();

	public static async downloadAsGLB(scene: Object3D)
	{
		const url = await this.getUrlFromScene(scene, "glb");
		FileUtils.downloadFileFromUrl(url, `containers-${DateUtils.getCurrentDateString()}.glb`);
	};

	public static getUrlFromScene(scene: Object3D, format: ModelFormat): Promise<string>
	{
		return new Promise<string>((resolve, reject) =>
		{
			const clone = THREEUtils.deepClone(scene);

			const options = {
				binary: true
			};

			const exporter = format === "glb" ? this._gltfExporter : this._usdzExporter;

			exporter.parse(clone, (result: ArrayBuffer) =>
			{
				const ret = FileUtils.createURLFromData(result, "application/octet-stream");
				resolve(ret);
			}, options);
		});
	}
}