import {Model} from "model/Model";
import {ConfirmWindow} from "ui/popup/ConfirmWindow";
import {FormWindow, IFormWithConfig} from "ui/popup/FormWindow";
import {WarningWindow} from "ui/popup/WarningWindow";
import {Constants} from "utils/Constants";
import {HTMLUtils} from "utils/HTMLUtils";
import {StringUtils} from "utils/StringUtils";
import {XHRLoader} from "utils/XHRLoader";
import {SceneManager} from "./SceneManager";

export class UI
{
	private _startTypeChooser: HTMLElement = document.getElementById("startTypeChooser");
	private _quoteBtn: HTMLElement = document.getElementById("quoteBtn");
	private _deleteBtn: HTMLElement = document.getElementById("deleteBtn");
	private _shareConfigBtn: HTMLElement = document.getElementById("shareBtn");
	private _urlParams = new URLSearchParams(window.location.search);
	private _configuratorDiv: HTMLElement = document.getElementById("configurator");
	private _modelViewerDiv: HTMLElement = document.getElementById("modelViewer");
	private _enterARButton: HTMLElement = document.getElementById("enterARButton");
	private _exitARButton: HTMLElement = document.getElementById("exitARButton");
	private _modelViewerElement: HTMLElement = null;

	private _model: Model;
	private _sceneManager: SceneManager;

	constructor(model: Model, sceneManager: SceneManager)
	{
		this._model = model;
		this._sceneManager = sceneManager;

		this.init();
	}

	private async init()
	{
		this._sceneManager.signals.onContainerSelected.add(this.updateDeleteBtn);

		this._enterARButton.onclick = async () =>
		{
			this._sceneManager.cameraControls.stopRotating();
			this._modelViewerElement = await this.createModelViewerElement();
			this._modelViewerDiv.appendChild(this._modelViewerElement);
			this._modelViewerDiv.classList.remove("hidden");
			this._configuratorDiv.classList.add("hidden");
			this._sceneManager.deselectAll();
		};

		this._exitARButton.onclick = () =>
		{
			HTMLUtils.detach(this._modelViewerElement);
			this._modelViewerDiv.classList.add("hidden");
			this._configuratorDiv.classList.remove("hidden");
		};

		this._quoteBtn.onclick = this.onQuoteBtnClick;

		this._deleteBtn.onclick = async () =>
		{
			const {selectedContainer} = this._sceneManager.selectionTool;
			if (selectedContainer)
			{
				const containersToDelete = this._sceneManager.getContainersAboveSelected();

				const confirmed = await ConfirmWindow.open(`${containersToDelete.length} Container löschen? Bestätigen, um fortzufahren.`);
				if (confirmed)
				{
					for (const container of containersToDelete)
					{
						this._model.removeContainer(container);
					}
				}
			}
		};

		this._shareConfigBtn.onclick = async () =>
		{
			const config = JSON.stringify(this._model.containers);

			const response = await XHRLoader.loadAsync<{configId: string}>({
				url: `${XHRLoader.BASE_URL}/save_config.php`,
				method: XHRLoader.METHOD_POST,
				params: {
					config: config
				}
			});

			const link = `${this.getBrowserBaseUrl()}?config=${response.configId}`;

			await WarningWindow.open(
				`<p>Mit dem folgenden Link, können Sie Ihre Konfiguration teilen:</p>
				<br>
				<a href="${link}" target="_blank">${link}</a>`,
				{ok: "Schliessen", backdrop: false}
			);
		};

		const configId = this._urlParams.get("config");

		if (configId)
		{
			const configUrl = `${this.getBrowserBaseUrl()}php/configs/${configId}.txt`;

			try
			{
				const configString = await XHRLoader.loadAsync<string>({
					url: configUrl,
					method: XHRLoader.METHOD_GET,
					json: false
				});

				this._model.loadConfig(JSON.parse(configString));

				this.initTools();
			}
			catch (error)
			{
				await WarningWindow.open("Konfiguration konnte nicht gefunden werden.");
				this.startFromScratch();
			}
		}
		else
		{
			this.startFromScratch();
		}
	}

	private startFromScratch()
	{
		const dropDown = this._sceneManager.createDropDown([0, 0, 0], true);
		this._startTypeChooser.appendChild(dropDown);
		this._startTypeChooser.classList.remove("hidden");

		this._model.signals.configLoaded.add(this.removeStartTypeChooserAndActivateTools);
		this._model.signals.containerAdded.add(this.removeStartTypeChooserAndActivateTools);
	}

	// without the query part
	private getBrowserBaseUrl()
	{
		return `${location.protocol}//${location.hostname}${location.pathname}`;
	}

	private removeStartTypeChooserAndActivateTools = () =>
	{
		this._model.signals.configLoaded.remove(this.removeStartTypeChooserAndActivateTools);
		this._model.signals.containerAdded.remove(this.removeStartTypeChooserAndActivateTools);

		this.initTools();
	}

	private initTools()
	{
		this._sceneManager.cameraControls.activate();
		this._sceneManager.selectionTool.activate();
		HTMLUtils.detach(this._startTypeChooser);
	}

	private async createModelViewerElement()
	{
		// <model-viewer src="shared-assets/models/Astronaut.glb" alt="A 3D model of an astronaut" auto-rotate camera-controls></model-viewer>
		const glbUrl = await this._sceneManager.getObjectUrlFromConfig("glb");
		const usdzUrl = await this._sceneManager.getObjectUrlFromConfig("usdz");

		const element = document.createElement("model-viewer");
		element.setAttribute("src", glbUrl);
		element.setAttribute("ios-src", usdzUrl);
		element.setAttribute("alt", "Containers");
		element.setAttribute("ar-placement", "floor");
		element.setAttribute("ar-modes", "webxr scene-viewer quick-look");
		element.toggleAttribute("ar", true);
		element.toggleAttribute("camera-controls", true);

		return element;
	}

	public updateDeleteBtn = () =>
	{
		const containersAboutToDelete = this._sceneManager.getContainersAboveSelected();

		if (0 < containersAboutToDelete.length && containersAboutToDelete.length < this._sceneManager.containers.length)
		{
			this._deleteBtn.classList.remove("disabled");
		}
		else
		{
			this._deleteBtn.classList.add("disabled");
		}
	};

	private onQuoteBtnClick = async () =>
	{
		const form = await FormWindow.open(
			`<form id="form" class="form vbox">
				<div class="vbox"><label for="firstName">Vorname:</label><input id="firstName"></div>
				<div class="vbox"><label for="surName">Nachname:</label><input id="surName"></div>
				<div class="vbox"><label for="company">Company:</label><input id="company"></div>
				<div class="vbox"><label for="postCode">PLZ & Ort:</label><input id="postCode"></div>
				<div class="vbox"><label for="phone">Telefon:</label><input id="phone"></div>
				<div class="vbox"><label for="email">E-mail:</label><input id="email" type="email" required></div>
				<div class="vbox"><label for="message">Ihre Nachricht:</label><textarea id="message" rows="4"></textarea></div>
			</form>`
		);

		if (StringUtils.isEmailAddressValid(form?.email || ""))
		{
			const config = JSON.stringify(this._model.containers);

			const baseUrl = this.getBrowserBaseUrl();

			const dataToSend: IFormWithConfig = {
				...form,
				config: config,
				baseUrl: baseUrl
			};

			const response = await XHRLoader.loadAsync<{configId: string}>({
				url: `${XHRLoader.BASE_URL}/send_quote.php`,
				method: XHRLoader.METHOD_POST,
				params: dataToSend
			});

			const link = `${baseUrl}?config=${response.configId}`;

			await WarningWindow.open(
				`<h2>Besten Dank für Ihr Interesse an unse­ren Produkten.</h2><br>
				<p>Wir haben Ihre Anfrage erhal­ten und wer­den Sie umge­hend bearbeiten oder uns bei Ihnen melden.</p><br><br>
				<p>Mit dem folgenden Link, können Sie Ihre Konfiguration teilen:</p>
				<br>
				<a href="${link}" target="_blank">${link}</a>`,
				{ok: "Schliessen", backdrop: false}
			);
		}
	};
}