import {StringUtils} from "utils/StringUtils";
import {PopupWindow} from "./PopupWindow";

interface IForm
{
	firstName: string;
	surName: string;
	company: string;
	postCode: string;
	phone: string;
	email: string;
	message: string;
}

export interface IFormWithConfig extends IForm
{
	config: string;
	baseUrl: string;
}

export class FormWindow extends PopupWindow<IForm>
{
	protected static readonly _defaultConfig = {
		ok: "Senden",
		cancel: "Abbrechen",
		backdrop: false
	};

	protected _cancelValue = null;
	protected _okValue = {
		firstName: "",
		surName: "",
		company: "",
		postCode: "",
		phone: "",
		email: "",
		message: ""
	};

	constructor(message: string)
	{
		super({
			message: message,
			config: FormWindow._defaultConfig
		});

		this.disableOkButton();
	}

	private updateOkValueFromFormValues()
	{
		for (const key in this._okValue)
		{
			this._okValue[key] = (document.getElementById(key) as HTMLInputElement).value || "";
		}
	}

	protected updateOkButton = () =>
	{
		this.updateOkValueFromFormValues();

		const emailElement = document.getElementById("email") as HTMLInputElement;

		if (StringUtils.isEmailAddressValid(this._okValue.email))
		{
			emailElement.classList.remove("invalid");
			this.enableOkButton();
		}
		else
		{
			if (emailElement.value.length > 0)
			{
				emailElement.classList.add("invalid");
			}

			this.disableOkButton();
		}
	};

	public static open(message: string)
	{
		return new FormWindow(message).open();
	}
}