import {Box3, BoxGeometry, Mesh, MeshBasicMaterial, Object3D} from "three";
import {THREEUtils} from "utils/THREEUtils";
import {IContainer} from "../model/Model";
import {SceneManager} from "./SceneManager";

export class Container
{
	private static _boxGeometry = new BoxGeometry();
	private static _invisibleMaterial = new MeshBasicMaterial({transparent: true, opacity: 0});
	private _sceneManager: SceneManager;
	private _object3D: Object3D;
	private _modelData: IContainer;
	private _bbox: Box3;
	private _intersectable: Mesh;

	constructor(sceneManager: SceneManager, modelData: IContainer)
	{
		this._sceneManager = sceneManager;
		this._modelData = modelData;

		this._object3D = this._sceneManager.createNewContainer(modelData.type);
		this._bbox = new Box3().setFromObject(this._object3D);
		const size = this.size;
		this._object3D.position.x = modelData.location[0] * size.x;
		this._object3D.position.y = modelData.location[1] * size.y + size.y / 2;
		this._object3D.position.z = modelData.location[2] * size.z;
		this._intersectable = new Mesh(Container._boxGeometry, Container._invisibleMaterial);
		this._intersectable.scale.set(size.x, size.y, size.z);
		this._intersectable.position.copy(this._object3D.position);
		this._intersectable.updateMatrix();
		this._intersectable.updateMatrixWorld();
		this._intersectable.userData.modelData = this._modelData;
		this._object3D.userData.modelData = this._modelData;
		this._sceneManager.containerGroup.add(this._object3D);
	}

	public destroy()
	{
		this._sceneManager.containerGroup.remove(this._object3D);
	}

	// World coordinates
	public get position()
	{
		return this._object3D.position;
	}

	public get size()
	{
		return THREEUtils.getSizeOfBoundingBox(this._bbox);
	}

	public get modelData()
	{
		return this._modelData;
	}

	public get intersectable()
	{
		return this._intersectable;
	}
}