import {ObjectUtils} from "utils/ObjectUtils";
import {Signal} from "utils/Signal";

export interface IContainer
{
	type: number;
	location: number[]; // unique, x, y, z, values quantized to 1. NOT world coordinates
}

export class Model
{
	private _containers: IContainer[] = [];

	public signals = {
		containerAdded: Signal.create<IContainer>(),
		containerRemoved: Signal.create<IContainer>(),
		configLoaded: Signal.create<IContainer[]>()
	};

	public loadConfig(containers: IContainer[])
	{
		this._containers = containers;

		this.signals.configLoaded.dispatch(this._containers);
	}

	public addContainer(container: IContainer)
	{
		this._containers.push(container);
		this.signals.containerAdded.dispatch(container);
	}

	public removeContainer(container: IContainer)
	{
		if (container)
		{
			const index = this._containers.findIndex((c: IContainer) => ObjectUtils.compare(c, container));
			if (index > -1)
			{
				this._containers.splice(index, 1);
				this.signals.containerRemoved.dispatch(container);
			}
		}
	}

	public get containers()
	{
		return this._containers;
	}
}